import React, { useState, useEffect } from "react";
import MenuItem from "../Components/MenuItem";
import NotificationsIcon from "@material-ui/icons/Notifications";
import AddAlertIcon from "@material-ui/icons/AddAlert";
import BookmarkTwoToneIcon from "@material-ui/icons/BookmarkTwoTone";
import StockQuote from "../Components/StockQuote";
import Card from "@material-ui/core/Card";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardHeader from "@material-ui/core/CardHeader";
import IconButton from "@material-ui/core/IconButton";
import getETHours from "../UsefullFunctions/getETHours";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import ShowChartIcon from "@material-ui/icons/ShowChart";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { changeTradingPageSymbol } from "../WebPages/TradingPage/Redux/actions";

const mapDispatchToProps = (dispatch) => {
  return {
    changeTradingPageSymbol: (symbol) =>
      dispatch(changeTradingPageSymbol(symbol)),
  };
};

const mapStateToProps = (state) => {
  return {
    isUserLoggedIn: state.loginStatus.userIsLoggedIn,
    watchlists: state.watchlists.data,
    events: state.events,
    isUserDataPending: state.userDataObj.isPending,
    stocksPageSymbol: state.stocksPage.symbol,
  };
};

const isStockOpen = () => {
  let time = new Date();

  let hr = getETHours(time);

  const verifyDate = (time) => {
    let day = time.getUTCDay();
    let date = time.getUTCDate();

    if (day === 6) {
      date = date - 1;
    } else if (day === 0) {
      date = date - 2;
    }

    date = date + "/" + time.getUTCMonth();
    return date;
  };

  if (hr >= 9 && hr <= 16) {
    return (
      <Typography variant="subtitle2" align="left">
        Open {verifyDate(time)} 09:00 EDT
      </Typography>
    );
  }
  return (
    <Typography variant="subtitle2" align="left">
      Closed {verifyDate(time)} 16:00 EDT
    </Typography>
  );
};

const StockInfoBar = ({
  stockDetails,
  stocksPageSymbol,
  changeTradingPageSymbol,
}) => {
  return (
    <div>
      <StockQuote symbol={stocksPageSymbol} />
      <Card>
        <CardContent> {isStockOpen()}</CardContent>{" "}
      </Card>

      <div>
        <Card>
          <CardActions>
            <IconButton aria-label="add to watchlist">
              <BookmarkTwoToneIcon />
            </IconButton>

            <IconButton aria-label="alerts">
              <AddAlertIcon />
            </IconButton>
            <Link to="/app/trade" style={{ textDecoration: "none" }}>
              <Button
                variant="contained"
                color="default"
                startIcon={<ShowChartIcon />}
                onClick={() => {
                  changeTradingPageSymbol(stocksPageSymbol);
                }}
              >
                {" "}
                Trade
              </Button>
            </Link>
          </CardActions>
        </Card>
        <Card>
          <CardHeader title="Company Financials" subheader="" />
          <CardContent>
            <Grid container>
              <Grid item xs>
                <Typography variant="subtitle1">Stuff</Typography>
              </Grid>
              <Grid item xs>
                <Typography variant="subtitle1">More Stuff</Typography>
              </Grid>
              <Grid item xs>
                <Typography variant="subtitle1"> . . .</Typography>
              </Grid>
            </Grid>
          </CardContent>
        </Card>
      </div>
    </div>
  );
};

export default connect(mapStateToProps, mapDispatchToProps)(StockInfoBar);
