import React, { useState, useEffect } from "react";
import { TypeChooser } from "react-stockcharts/lib/helper";
import CandleStickChart from "./CandleStickChart";
import LineChart from "./LineChart";
import MaxDiv from "../MaxDiv";
import ChartContainer from "./ChartContainer";
import ErrorBoundary from "../ErrorBoundary";

const StockChart = ({ chartType, ...rest }) => {
  const getChartForContainer = () => {
    switch (chartType) {
      case "LineChart":
        return ({ chartData }) => (
          <TypeChooser>
            {(type) => <LineChart type={type} data={chartData} />}
          </TypeChooser>
        );

      case "CandleStickChart":
        return ({ chartData }) => (
          <TypeChooser>
            {(type) => <CandleStickChart type={type} data={chartData} />}
          </TypeChooser>
        );
      default:
        return <>{new Error("chartType is not Specified")}</>;
    }
  };

  return (
    <MaxDiv>
      <ErrorBoundary>
        <ChartContainer {...rest}>{getChartForContainer()}</ChartContainer>
      </ErrorBoundary>
    </MaxDiv>
  );
};

export default StockChart;
